import React from "react";
import "./ModuleCard.css";
import { Fade } from "react-reveal";
import { style } from "glamor";

function ModuleCard(props) {
  const module = props.module;
  const theme = props.theme;
  const styles = style({
    boxShadow: `0px 2px 5px ${module.color_code}`,
    border: `1px solid ${module.color_code}`,
    ":hover": {
      boxShadow: `0 5px 15px ${module.color_code}`,
    },
  });

  return (
    <Fade bottom duration={2000} distance="20px">
      <div className="module-card" {...styles}>
        <div
          className="content"
          style={{ backgroundColor: module.color_code }}
        >
          <a
            href={module.url}
            target="_blank"
            rel="noopener noreferrer"
          >
          </a>
        </div>
        <div className="module-body">
          <h2 className="module-body-title" style={{ color: theme.text }}>
            {module.title}
          </h2>
        </div>
      </div>
    </Fade>
  );
}

export default ModuleCard;
